.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}


.resp-nav{
  display: none;
}


@media (max-width: 900px) {
  .main-nav{
    display: none;
  } 
  .resp-nav{
    z-index: 10;
    display: block;
    position:sticky;
    top:auto;
  }
}